table{
    width: 100%;
}
th, td {
    border-bottom: 1px solid #ddd;
    border-left: 1px solid #ddd;
    padding: 20px;
}
td{
    text-align: center;
}
td:first-child, th:first-child {
    border-left: none;
}
th:first-child, th:first-child {
    border-left: none;
}
.game{
    width: 100%;
    height: 100%;
    display: flex;
    flex-flow: column;
}
.tableDiv{
    flex-grow : 1;
}
.selectedId{
    background-color: red;
}
.name{
    text-align: center;
    padding: 20px;
    font-size: 50px;
}
.text{
    text-align: center;
    font-size: 30px;
}
.winner{
    background-color: #3282b8;
}
.acabarDiv{
    text-align: center;
    margin: 10px 20px;
}
.acabarBtn{
    padding: 30px 60px;
    font-size: 30px;
}